@import "style/mixins.module.scss";

.root {
    position: relative;
    padding: 0 24px;

    @include respond-to("laptop") {
        display: grid;
        grid-template-columns: repeat(12, 1fr);
        grid-column-gap: 24px;
        align-content: center;
        min-height: 700px;
        height: 100vh;
        padding: 0 40px;
    }

    @include respond-to("laptop-L") {
        width: 1208px;
        margin: 0 auto;
        padding: 24px 0 0;
    }
}

.picture {
    display: block;

    @include respond-to("mobile") {
        margin-bottom: 32px;
    }

    @include respond-to("laptop") {
        grid-row: 1;
        grid-column: 8 / span 4;
    }

    &__image {
        width: 100%;
    }
}

.content {
    grid-row: 1;
    grid-column: 2 / span 5;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.logo {
    display: block;
    height: 28px;
    margin-bottom: 40px;

    &__icon {
        transform: scale(0.7) translateX(-20%) translateY(-10%);

        @include respond-to("laptop") {
            transform: scale(0.8) translateX(-12%) translateY(-18%);
        }
    }

    svg {
        width: 100%;
        height: 100%;
    }

    @include respond-to("laptop") {
        position: absolute;
        top: 24px;
        height: 32px;
    }
}

.title {
    margin-bottom: 16px;

    i {
        @include color("primary", "red");
        font-style: normal;
    }
}

.subtitle {
    margin-bottom: 24px;
    font-size: 17px;
    line-height: 24px;

    @include respond-to("laptop-L") {
        margin-bottom: 80px;
    }
}

.link {
    position: relative;
    display: flex;
    justify-content: space-between;
    margin-bottom: 64px;

    @media (max-width: 374px) {
        flex-direction: column;
    }
}

.loading-icon {
    margin: 0 auto;
}

.input {
    @include color("gray", "-4");

    flex-grow: 1;
    width: 187px;
    margin: 0;
    padding: 0;
    margin-right: 16px;
    border: none;
    border-bottom: 1px solid rgba(171, 180, 187, 0.2);
    text-overflow: ellipsis;
    outline: none;
    font-family: Inter, sans-serif;
    font-size: 17px;
    font-weight: normal;
    line-height: 24px;

    @media (max-width: 374px) {
        margin-right: 0;
        margin-bottom: 24px;
        width: 100%;
    }

    @include respond-to("laptop") {
        width: 210px;
        margin-right: 8px;
    }

    @include respond-to("laptop-L") {
        width: 312px;
    }
}

.button {
    justify-content: center;
    width: 132px;

    @media (max-width: 374px) {
        width: 100%;
    }

    @include respond-to("laptop") {
        width: 160px;
    }
}

.description {
    display: block;
    margin-bottom: 64px;
    font-size: 17px;
    line-height: 24px;

    @include respond-to("laptop") {
        margin-bottom: 0;
    }

    i {
        @include color("primary", "red");
        font-style: normal;
    }
}

.toast {
    top: -1px;
    bottom: -1px;
    width: 100%;
    z-index: -1;
    opacity: 0;
    transition: opacity 0.3s ease-out;
    background-color: get_color("gray", "0");
    box-shadow: 0px 6px 14px -6px rgba(24, 39, 75, 0.12),
        0px 10px 32px -4px rgba(24, 39, 75, 0.1);

    &_visible {
        z-index: 1;
        opacity: 1;
    }

    @media (max-width: 374px) {
        top: auto;
        height: 58px;
    }

    @include respond-to("laptop") {
        width: 160px;
        right: 0;
    }

    &__icon {
        @include color("gray", "0");

        display: flex;
        align-items: center;
        justify-content: center;
        width: 24px;
        height: 24px;
        margin-right: 8px;
        border-radius: 50%;
        background-color: get_color("secondary", "green");
    }
}

.footer {
    margin-bottom: 24px;

    @include respond-to("laptop") {
        position: absolute;
        bottom: 40px;
        margin: 0;
    }
}

.terms {
    margin: 0;
    font-size: 17px;
    line-height: 24px;

    i {
        @include color("primary", "blue");

        font-style: normal;
    }

    @include respond-to("laptop") {
        text-align: center;
    }
}

.error {
    @include color("primary", "red");

    margin: 0;
    font-size: 17px;
    line-height: 24px;
}
