.steps-list {
    display: flex;
    justify-content: center;
    padding: 0;
    margin-top: 0;
    margin-bottom: 16px;
    list-style-type: none;
}

.step {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 16px;
    width: 32px;
    height: 32px;
    border-radius: 32px;
    background-color: #eef3f4;
    color: #abb4bb;
    font-weight: 700;
    font-size: 17px;
    line-height: 24px;

    &_active {
        color: #45a1f8;
        border: 2px solid #45a1f8;
        background-color: transparent;
    }

    &_completed {
        background-color: #45a1f8;
        color: #ffffff;
    }

    &:last-child {
        margin-right: 0;
    }
}

.title {
    display: none;
    margin: 0;

    &_active {
        display: block;
    }
}

@media (min-width: 768px) {
    .title {
        margin-bottom: 24px;
    }
}
